import React from "react"
import thumb from "../images/thumb.png"
export default function DocInfo(prop) {
    return (
        <div className="docInfo">
            <div className="doctors">
                <img src="data:image/jpeg;base64,"alt="not present" className="doctor-img" />
                <div className="doctor-details">
                    <p className="docName"><b>Dr. {prop.FirstName} {prop.LastName}</b></p>
                    <p><b>{prop.Speciality}</b></p>
                    <p>{prop.Experience} Years of Experience</p>
                    <p><b>{prop.Address} - {prop.Pincode}</b></p>
                    <p>Rs. {prop.Fee} Consultation Fee</p>
                    <div className="docinfo-div1">
                        <button className="para"><img src={thumb} alt="not present"/>90%</button>
                        <p className="para">98 Patient stories</p>
                    </div>
                </div>
            </div>
            <div>
                <button type="click" className="docInfo-btn"><b>Book Clinic Visit</b> <p>No Booking Fee</p></button>
            </div>
        </div>
    )
}